body {
  background-color: rgb(147, 177, 201);
}
.title{
  text-align: center;
  padding: 2%;
  background-color: #0a3a6a;
  color: goldenrod;
}

.basic-qs {
  position: fixed;
  left: 20%;
  padding: 10px;
  padding-top: 10%;
}

button{
  background-image: radial-gradient(rgb(237, 174, 12), rgb(246, 218, 134)) ;
  color: #0a3a6a;
  border-radius: 8px;
  font-size: 20px;
}
button:hover{
  background-image: radial-gradient(rgb(202, 145, 1), rgb(250, 195, 32)) ;
}

.detailed-qs{
  position: fixed;
  right: 20%;
  padding: 10px;
  padding-top: 10%;
}

.api-key{
  left: 0;
  bottom: 0;
}
